

.landing-card.cs-iep {
    border-top-color: $cs-blue;
	&:before {
        content:'01';
        color: $cs-blue;
    }
// content block    
    .category {
        color: $cs-blue;
    }
    .logo-block {
        .logo-anthem {
            display:inline-block;
            height: 2.4rem;
            width: auto;
            margin-right:4.8rem;
        }
        .logo-ingenio {
            display:inline-block;
            height: 2.4rem;
            width: auto;
            margin-top: 0.6rem;
        }
    }
// link block   
    .block-link {
        .screen {
            overflow: hidden;
            z-index:4;
            position: absolute;
            top:12%;
            left:10%;
            bottom:10%;
            width:80%;
            height:auto;
            border-radius:.6rem;
        }
        .thumb {
            z-index:5;
            position:absolute;
            top:40%;
            left:0;
            width:16rem;
            border-radius:.8rem;
            overflow: hidden;
        }
        .arrow-icon-circle {
            background-color: $cs-blue;
        }
        .transparency {
            background-color: $cs-blue;
        }
        .transparency.pos01 {
            top:8%;
            right:30%;
            bottom:12%;
            left:6%;
            z-index:1;
        }
        .transparency.pos02 {
            top:4%;
            right:14%;
            bottom:8%;
            left:20%;
            z-index:2;
        }
        .transparency.pos03 {
            top:20%;
            right:4%;
            bottom:4%;
            left:30%;
            z-index:3;
        }
// hover        
        &:hover {
            .transparency.pos01 {transform: rotate(2deg) scale(.9,1.2); transform-origin: center left;}
            .transparency.pos02 {transform: rotate(-2deg) scale(1.1,1); transform-origin: top left;}
            .transparency.pos03 {transform: rotate(0) scale(1.2,1.1); transform-origin: bottom right;}
		}
    }
}


@media screen and (max-width: 680px) {
	.landing-card.cs-iep {
		.block-link {
			.screen {
				top: 2.4rem;
				left: 0;
				width: 100%;
			}
            .thumb {
                display:none;
            }
            .transparency.pos01 {
                top:1.2rem;
                right:30%;
                bottom:0;
                left:6%;
            }
            .transparency.pos02 {
                top:20%;
                right:-1.6rem;
                bottom:8%;
                left:20%;
            }
            .transparency.pos03 {
                top:20%;
                right:4%;
                bottom:-2.4rem;
                left:-1.6rem;
            }
		}
	}
}