/* ==========
CASE STUDY - CABIN SELECTION MODIFICATIONS
========== */

$yellow: #ffdf7f;
$tintYellow: #fff8e1;

.folio-cabin {
  .intro-block {
    background: $yellow
      url(../img/cruise-ship-in-dock-sunset-b.jpg) no-repeat
      left top;
    background-size: 100% auto;
    // background-attachment: fixed;
  }

  .pullquote {
    background-color: $tintYellow;
  }

  .cs-userflow {
    padding: 2.4rem;
  }

  .cs-ideation {
    @include flex-row;
    justify-content: space-around;

    height: 40rem;
    background-color: #ddd;
    margin-top: 4rem;

    .image {
      @include flex-column;
      justify-content: center;
      align-items: center;

      width: 40%;
      overflow: hidden;
    }
  }

  .cs-prototype {
    width: 320rem;
  }

  .cs-cruise-show {
    width: 100%;
    max-width: 88rem;
    margin-top: 4rem;
    margin-bottom: 4rem;

    .caption {
      display: block;
      text-align: center;
    }
  }

  .cs-chart {
    max-width: 48rem;
  }

  .cs-designV1 {
    width: 320rem;
  }

  .cs-designV2 {
    width: 200rem;
  }

  .insights-table {
    th {
    }

    td {
      border: 2px solid #fff;
      background-color: $yellow;
    }
  }

  .fa-minus-circle {
    fill: #666;
  }
  .fa-check-circle {
    fill: #1a7750;
  }
}

// BREAKPOINTS

@media screen and (max-width: 1400px) {
}

@media screen and (max-width: 1200px) {
  .cs-userflow {
    overflow-x: scroll;
    border-width: 1px;
    border-style: solid;
    border-color: #ddd;
    &:hover {
      // cursor: url(../img/ui-cursor-horiz-scroll.svg), move;
    }
    img {
      min-width: 120rem;
    }
  }
}

@media screen and (max-width: 960px) {
}

@media screen and (max-width: 860px) {
  .folio-cabin {
    .intro-block {
      background-size: cover;
    }
    .cs-ideation {
      .image {
        width: 50%;
      }
    }
  }
}

@media screen and (max-width: 680px) {
  .folio-cabin {
    .intro-block {
      min-height: 32rem;
      padding: 0 2.4rem;
      .title-block {
        padding: 2.4rem;
      }
    }
    .cs-ideation {
      @include flex-column;
      height: 60rem;
      .image {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .folio-cabin {
    .intro-block {
      background-size: 100% auto;
      padding: 0;
    }
  }
}
@media screen and (max-width: 375px) {
}
