/*--------------------------
ABOUT ME
--------------------------*/

.about-me {
  @include flex-row;
  justify-content: center;
  align-items: center;

  width: calc(100vw - 16rem);
  height: calc(100vh - 12rem);
  margin: 0 8rem 8rem 8rem;
  padding: 8rem 2.4rem;
  background-color: #eff7f6;

  .title {
    font-size: 4rem;
    margin-bottom: 1.6rem;
  }

  .content {
    width: 100vw;
    max-width: 60rem;
  }

  .inner {
    @include flex-row;
  }

  .text-block {
    width: 75%;
    padding-right: 4rem;
  }

  .profile-pic {
    width: 25%;
    border-radius: 0.4rem;
    overflow: hidden;
  }
}

// BREAKPOINTS
@media screen and (max-height: 800px) {
  .about-me {
    height: auto;
  }
}

@media screen and (max-width: 1400px) {
  .about-me {
    width: calc(100vw - 8rem);
    margin-right: 4rem;
    margin-left: 4rem;
  }
}

@media screen and (max-width: 960px) {
  .about-me {
    height: auto;
  }
}

@media screen and (max-width: 860px) {
}

@media screen and (max-width: 680px) {
  .about-me {
    width: calc(100vw - 3.2rem);
    margin: 0 1.6rem 4rem 1.6rem;
  }
}

@media screen and (max-width: 480px) {
  .about-me {
    .inner {
      @include flex-column;
    }

    .text-block {
      width: 100%;
      padding-right: 0;
    }

    .profile-pic {
      width: 25%;
      border-radius: 0.4rem;
      overflow: hidden;
    }
  }
}
