/* ==========
CASE STUDY TEMPLATE
========== */

.case-study {
  @include flex-column;
  align-items: center;

  width: 100%;

  .intro-block {
    @include flex-row;
    justify-content: center;
    align-items: flex-end;

    width: 100%;

    min-height: 60rem;
    padding: 0 4rem;
    margin-bottom: 4rem;

    .inner {
      @include flex-row;
      justify-content: center;

      width: 100%;
      max-width: 120rem;
    }

    .title-block {
      width: 100%;
      max-width: 68rem;
      padding: 4rem;
      background-color: #ffdf7f;
    }

    .title {
      font-size: 4.8rem;
      line-height: 1.1;
      margin-bottom: 1.2rem;
    }

    .tag-list {
      @include flex-row;
    }

    .tag {
      position: relative;

      &:first-child:after {
        content: "//";
        margin: 0 0.8rem;
      }
    }
  }

  .intro-para {
    font-size: 2.4rem;
  }

  .text-block {
    width: 100%;
    max-width: 64.8rem;
    padding: 0 2.4rem;

    .title {
      font-size: 2.4rem;
      margin-top: 2.4rem;
      margin-bottom: 1.6rem;
    }

    .list-item {
      font-size: 1.8rem;
      margin-bottom: 0.8rem;
      padding-left: 2rem;
      position: relative;

      &:before {
        width: 1rem;
        height: 0.2rem;
        background-color: $cyan;
        position: absolute;
        top: 1.2rem;
        left: 0;
        content: "";
      }

      &.withIcon {
        .svg-inline--fa {
          position: absolute;
          top: 1rem;
          left: 0;
        }

        &:before {
          display: none;
        }
      }
    }

    .fa-times-circle {
      height: 1.2rem;
      margin-right: 0.8rem;
      fill: #dd173d !important;
    }
  }

  .pullquote {
    width: 100%;
    max-width: 88rem;
    background-color: #ddd;
    margin-top: 8rem;
    margin-bottom: 8rem;
    padding: 8rem;

    .quote {
      display: block;
      font-weight: 300;
      font-size: 4rem;
      text-align: center;
      line-height: 1.2;
      position: relative;

      .fa-quote-left,
      .fa-quote-right {
        height: 1.6rem;
        margin-right: 0.4rem;
        opacity: 0.25;
      }

      .fa-quote-right {
        margin-left: 2rem;
      }
    }

    .caption {
      text-align: center;
      display: block;
      width: 100%;
      max-width: 44rem;
      margin: 1.2rem auto 0;
    }

    .list {
      @include flex-row;
      justify-content: center;
      flex-wrap: wrap;
    }

    .list-item {
      display: inline-block;
      white-space: nowrap;

      &:after {
        content: "//";
        margin: 0 1rem;
      }

      &:last-child:after {
        display: none;
      }
    }
  }

  .image-block {
    width: 100%;
    max-width: 140rem;
    margin-bottom: 8rem;
  }

  .prototype-block {
    width: 100%;
    overflow-x: scroll;

    &:hover {
      cursor: url(../img/ui-cursor-horiz-scroll.svg), move;
    }
  }

  .prototype-list {
    @include flex-row;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: #eee;
    padding: 2.4rem 8rem;
  }

  .prototype-item {
    display: block;
    margin-right: 3.2rem;

    .caption {
      font-size: 1.2rem;
      margin-top: 1.6rem;
      display: block;
    }
  }

  .prototype-screen {
    width: 100%;
    max-width: 40rem;

    img {
      @include shadow01;
    }
  }

  .table-block {
    width: 100%;
    overflow-x: scroll;
  }

  .insights-table {
    width: 100%;

    th {
      padding: 0.8rem 1.6rem;
      white-space: nowrap;
    }

    td {
      padding: 0.8rem 1.6rem;
      white-space: nowrap;

      &:first-child {
        white-space: wrap;
      }
    }
  }

  .fa-desktop,
  .fa-tablet-alt,
  .fa-mobile-alt,
  .fa-check-circle,
  .fa-minus-circle {
    fill: #000;
    height: 1.4rem;
    margin: -0.4rem 0.6rem 0 0;
  }
}

// BREAKPOINTS

@media screen and (max-width: 1400px) {
  .case-study {
    .intro-block {
      min-height: 40rem;
    }
  }
}

@media screen and (max-width: 1100px) {
}

@media screen and (max-width: 960px) {
}

@media screen and (max-width: 860px) {
  .case-study {
    .intro-block {
      .title {
        font-size: 4rem;
      }
    }
  }
}

@media screen and (max-width: 680px) {
  .case-study {
    .pullquote {
      padding: 8rem 2.4rem;
    }
    .table-block {
      box-shadow: 8px 2px 8px -4px rgba(0, 0, 0, 0.4);
    }
  }
}

@media screen and (max-width: 480px) {
  .case-study {
    .intro-block {
      .title {
        font-size: 3.2rem;
      }
    }
  }
}

@media screen and (max-width: 375px) {
