/* ==========
CASE STUDY TABS
========== */

.tabs-component {
    width:100%;
    margin-top:-2rem;

    .tabs-nav {
        @include flex-row;
        justify-content: center;
        padding-left: 0;
        list-style: none;
        margin-bottom:2.4rem;
        position:relative;
    }

    .tab-list-item {
        font-weight: 700;
        padding: .8rem 1.6rem;
        position: relative;

        &:hover {
            cursor:pointer;
            background-color: rgba(0,0,0,.1)
        }

        &:first-child:before,
        &:after {
            position:absolute;
            top:0;
            right:0;
            width: 2px;
            height:100%;
            background-color:#fff;
            content:'';
        }
        &:first-child:before {
            right:auto;
            left: 0;
        }
    }

    .tab-list-active {
        color: #fff;
        background-color: $cyan;

        &:hover {
            background-color: $turq;
        }
    }

    .tab-content {
        @include flex-row;
    }

}
