/* ==========
CASE STUDY TEMPLATE
========== */

$default-header-block: #ffc670;
$default-figure-block: #F0F0EC;
$dot-color: #d00000;

.case-study-template {
// HEADER
    .header {
        position:relative;
    }
    .header-title-block {
        position:absolute;
        left:0;
        bottom:0;
        width:100%;
        padding-left:28rem;

        .inner {
            width:100%;
            max-width: 72rem;
            margin:0 auto;
            padding:4rem;
            border-radius: .4rem .4rem 0 0;
            background-color: $default-header-block;
            display:block;
        }
    }
    .case-study-tags {
        font-size:1.6rem;
        font-weight:700;
    }
    .case-study-title {
        font-size:4.8rem;
        line-height:1.2;
        margin:0;
    }
    .hero-image {
        width: 100%;
        height:40rem;
        overflow: hidden;
        position:relative;
    }
// PAGE
    .container {
        @include flex-row;
        align-items:flex-start;
        z-index:10;
        max-width: unset;
        padding:4rem 2.4rem;
        position:relative;
    }

// PAGE NAV
    .sidebar { 
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        left:2.4rem;
        width:28rem;
        padding-top:4rem;
        
        .link-home {
            display:block;
            margin-bottom:4rem;

            &:hover {
                color:$cyan;
            }

            .fa-chevron-left {
                fill: $cyan;
                height:1.2rem;
                margin-right:.8rem;
                display:inline-block;
            }
        }
        .block-title {
            margin-bottom:.8rem;
        }
    }
    .sectionNav { 

        &:hover .link {
            color:#000;
        }  
        .link {
            padding:1.2rem 0;
            color:#888;

            &:hover .link-title {
                color:$cyan;
            }
            .selected {
                background-color:red;
            }
        }
        .link-section {
            font-size:1.2rem;
            font-weight:700;
            text-transform: uppercase;
            letter-spacing:.2rem;
            margin-bottom:.4rem;
            display:block;
        }
        .link-title {
            font-size:1.6rem;
            line-height:1.2;
            display:block;
        }
    }

// PAGE CONTENT
    .content {
        width: 100%;
    }
    .content-block {
        width:100%;
        max-width: 72rem;
        margin:0 auto;
        padding:4rem;
    }
    .title-block {
        @include flex-row;
        justify-content:center;
        position:absolute;
        z-index:1;
        left:0;
        bottom:0;
        width:100%;

        .title {
            width:100%;
            padding:3.2rem;
            margin:0;
        }
        .tag {
            display:block;
            font-size:1.4rem;
            letter-spacing: .2rem;
            text-transform: uppercase;
            margin-bottom:.8rem;
        }
        .h2 {
            display:block;
            font-size:4rem;
            font-weight:700;
            line-height:1.1;
        }
    }

// CONTENT SUMMARY
    .skills-list {
        @include flex-row;
        flex-wrap: wrap;
        margin-bottom:4rem;

        .skill-tag {
            line-height:1;
            padding:1.2rem 1.6rem;
            margin: 0 1.2rem 1.2rem 0;
            border: 1px solid #ddd;
            border-radius:.4rem;
            overflow: hidden;
            position: relative;

            &:before {
                position: absolute;
                top:0;
                left:0;
                bottom:0;
                width:.4rem;
                height:100%;
                content:'';
            }
        }
    }

// COMPONENT - DESIGNS
    .figure-block {
        counter-reset: dots; 
        padding-left:8rem;
        margin-bottom:4rem;

        .inner {
            @include flex-row;
            justify-content:space-between;

            width:100%;
            border-radius: .4rem;
            background-color: $default-figure-block;
            padding:2rem;
        }
        .captions-list {
            counter-reset: captions; 
            list-style:none;
            margin:2rem;
            padding:0;

            .caption {
                line-height:1.2;
                margin-bottom:1.6rem;
                padding-left:2.8rem;
                position:relative;

                &:before {
                    counter-increment: captions;
                    content: counter(captions);
                    font-size:1.2rem;
                    font-weight:700;
                    text-align:center;
                    position:absolute;
                    top:0;
                    left:0;
                    width:2rem;
                    height:2rem;
                    background-color:#fff;
                    border-radius:50%;
                    padding-top:.2rem;
                }
            }
        }
        .screen {
            overflow:hidden;
            border: 1rem solid #fff;
            border-radius:.4rem;
            position:relative;
        }
    }

// COMPONENT - TABLES
    .table-border {
        overflow:hidden;
        border: 2px solid #eee;
        border-radius: .8rem;
        display:block;

        &.insights {
            margin-top:2.4rem;
            border-color:#eee;
        }
    }
    .table {
        th {
            border-style:none;
            border-bottom: 1px solid #eee;
        }
    }

}
// close wrapper class
.table-block {
        width: 100%;
        overflow-x: scroll;
        margin-top:4rem; 
        margin-bottom:4rem; 
    }
.table-insights {
    width: 100%;
    th {
        border-bottom: 1px solid #fff;
        padding: 0.8rem 1.6rem;
        white-space: nowrap;
    }
    td {
        border-bottom: 2px solid #fff;
        padding: 1.6rem;
        background-color: rgba(255, 255, 255, 0.1);
        white-space: nowrap;

        &:first-child {
            white-space: wrap;
        }
    }
    .fa-desktop,
    .fa-tablet-alt,
    .fa-mobile-alt,
    .fa-check-circle,
    .fa-minus-circle {
        height: 1.4rem;
        margin: -0.4rem 0.6rem 0 0;
    }
    .fa-check-circle {
        fill: $green;
    }
    .fa-minus-circle {
        fill: #888;
    }
}

[data-dot] {
    position:absolute;
    width: 2.8rem;
    height: 2.8rem;
    border-radius: 50%;
    border: 4px solid #fff;
    background-color: $dot-color;
    box-shadow: 1px 1px 1px 1px rgba(0,0,0,.2);

    font-style: normal;
    font-weight: 700;
    color: #fff;
    text-align:center;

    &:before {
        counter-increment: dots;
        content: counter(dots);
    }
}

// BREAKPOINTS
// height
@media screen and (max-height: 800px) {


}
// width
@media screen and (max-width: 1400px) {
    .case-study-template {
        .hero-image {
            img {
                width: auto;
                height: 48rem;
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    .case-study-template {
        .header-title-block {
            padding-left:0;
        }
    }
}

@media screen and (max-width: 960px) {
    .case-study-template {
        .header-title-block {
            padding-left:0;
        }
        .container {
            padding: 2.4rem;
        }
        .sidebar {
            display:none;
        }
        .content-block {
            padding: 0;
        }
        .quote {
            margin-bottom:4rem;
        }
    }
}


@media screen and (max-width: 860px) {
    .case-study-template {
        .hero-image {
            height: 24rem;
            img {
                height: 26rem;
            }
        }
        .header-title-block {
            padding-left:2.4rem;
            padding-right:2.4rem;
            .inner {
                padding:2.4rem;
            }
        }
        .case-study-title {
            font-size: 3.6rem;
        }
    }
}

@media screen and (max-width: 680px) {
    .case-study-template {
        .quote {
            padding-left:0;
            padding-right:0;
            .fa-angle-double-left,
            .fa-angle-double-right {
                width: 2rem;
                top: .4rem;
                left: 0;
            }
            .fa-angle-double-right {
                left: auto;
                top:auto;
                right:0;
                bottom:.4rem;
            }
        }
        .table-block {
            box-shadow: 8px 2px 8px -8px rgba(0, 0, 0, 0.2);
        }
    }
}

@media screen and (max-width: 480px) {
    .case-study-template {
        .hero-image {
            height: auto;
            img {
                width:100%;
                height: auto;
            }
        }
        .header-title-block {
            position:relative;
            top:auto;
            bottom:auto;
            padding-left:0;
            padding-right:0;
            .inner {
                padding:2.4rem;
            }
        }
        .case-study-title {
            font-size: 2.8rem;
        }
    }
}

@media screen and (max-width: 375px) {
