/* ==========
IGLU SEARCH - MODIFY TEMPLATE
========== */

$header-block: #ffc670;
$figure-block: #F0F0EC;
$title-meta: #f2a22a;
$yellow-tint: #ffdf7f;

.case-study-template.iglu-search {
    .header-title-block {
        .inner {
            background-color:$header-block;
        }
    }
    .skills-list {
        .skill-tag {
            &:before {
                background-color: $header-block;
            }
        }
    }
// PAGE NAV
    .sectionNav {
        .link {
            &:hover .link-section  {
                color:$title-meta;
            }
        }
    }
// PAGE CONTENT
    .section-title-meta {
        color: $title-meta;
    }
    .figure-block {
        .inner {
            background-color: $figure-block;
        }
    }
    .quote {
        .statement { border-top-color: $header-block; }
        .personna { border-bottom-color: $header-block; }
        .fa-angle-double-left,
        .fa-angle-double-right {fill: $header-block;}
    }
    .previous-screens {
        .inner {
            @include flex-row;
            justify-content: space-between;
            align-items:flex-start;
        }
        .captions-list {
            width: 30rem;
        }
        .desktop {
            margin-right:.8rem;
            [data-dot="1"] {top: 6%; left: 22%; }
            [data-dot="2"] {top: 18%; left: 0px; }
            [data-dot="3"] {top: 36%; left: 6%; }
            [data-dot="4"] {top: 56%; left: 30%; }
            [data-dot="5"] {top: 50%; left: 84%; }
        }
        .mobile {
            [data-dot="6"] { top: 25%; left: 40%;}
        }
    }
    .table-border {
        border-color: $header-block;
    }
    .table {
        text-align: center;

        th {
            border-bottom-color: $header-block;
        }
    }

    .questions-list {
        text-align: center;
        font-size: 2rem;
        line-height: 1.2;
        padding: .8rem;

        li {
            margin-bottom:1.2rem;
        }
    }

    .tabs-nav {
        li {
            a {
                &:hover {
                    color:$header-block;
                }
            }
        }
    }

    .table-insights {
        td {
            background-color: $yellow-tint;
        }
        .fa-check-circle {
            fill: $midgreen;
        }
    }
}
// close main wrapper class

.search-card-design {

    .tab-content {
        justify-content: space-between;
        align-items: flex-start;
    }
    .captions-list {
        width:100%;
        max-width:32rem;
    }
    .card01 {
        [data-dot="1"] {top:0%; left:8%;}
        [data-dot="2"] {top:0; left:30%;}
        [data-dot="3"] {top:10%; left:80%;}
    }
    .card02 {
        [data-dot="1"] {top:0%; left:19%;}
        [data-dot="2"] {top:10%; left:55%;}
        [data-dot="3"] {top:10%; left:75%;}
    }
    .card03 {
        [data-dot="1"] {top:0%; left:13%;}
        [data-dot="2"] {top:60%; left:18%;}
        [data-dot="3"] {top:4%; left:80%;}
    }
    .card04 {
        [data-dot="1"] {top:75%; left:15%;}
    }
    .card05 {
        [data-dot="1"] {top:70%; left:15%;}
        [data-dot="2"] {top:80%; left:30%;}
        [data-dot="3"] {top:40%; left:85%;}
    }

}

.search-template-design {
    .captions-list {
        width:100%;
        max-width:32rem;
    }
    .screen {
        [data-dot="1"] {top:0%; left:8%;}
        [data-dot="2"] {top:65%; left:10%;}
        [data-dot="3"] {top:6%; left:40%;}
        [data-dot="4"] {top:12%; left:58%;}
        [data-dot="5"] {top:2%; left:82%;}
    }
}


// BREAKPOINTS
@media screen and (max-width: 1400px) {
    .case-study-template.iglu-search {
        .figure-block {
            .captions-list {
                margin-left:0;
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .case-study-template.iglu-search {
        .previous-screens .inner {
            flex-wrap: wrap;
            .captions-list {order:3; width:100%; max-width:40rem;}
            .desktop {order:1; width:65%;}
            .mobile {order:2; width:30%;}
        }
    }
    .search-card-design .tabs-component {
        .tab-content {
            @include flex-column;
        
            .captions-list {
                width:100%;
                max-width:32rem;
                order:2;
            }
            .screen {
                order:1;
            }
        }
    }
    .search-template-design.figure-block {
        .inner {
            @include flex-column;

            .captions-list {
                width:100%;
                max-width:32rem;
                order:2;
            }
            .screen {
                order:1;
            }
        }
    }
}

