@media screen and (max-width: 580px) {
    .case-study-template.iglu-search {
        .previous-screens .inner {
            @include flex-column;
            align-items: center;
            .captions-list {width:100%; max-width:40rem;}
            .desktop {width:100%; margin:0 0 2.4rem 0;}
            .mobile {width:100%; max-width:24rem;}
        }
    }
    .search-card-design .tabs-component {
        .tabs-nav {
            justify-content: flex-start;
            overflow-x: scroll;
        }
    }
}

