

.landing-card {
	@include flex-row;
	justify-content: flex-start;
	align-items: flex-start;

	border-top-width: .4rem;
	border-top-style: solid; 
	width: calc(100vw - 16rem);
	margin: 4rem 8rem 12rem;
	position: relative;
    &:before {
        position:absolute;
        top:0;
        left:0;
		font-size:7.2rem;
        font-weight:700;
		line-height: 1;
        padding-top:1.2rem;
    }
// content block	
	.block-content {
        width:50%;
		padding:10rem 4% 4rem 8%;
	}
	.title {
		font-size: 5.6rem;
		line-height: 1.1;
		margin-top: 0;
		margin-bottom: 3.2rem;
	}
	.category {
		font-weight: 600;
		font-size: 1.8rem;
		line-height: 1;
		margin-bottom: 1.2rem;
		display: block;
	}
	.logo-block {
		border-top:1px solid #ddd;
		display:inline-block;
		padding-top:1.6rem;
    }
// link block
	.block-link {
		position:absolute;
		top:0;
		right:0;
		width:50%;
		height:100%;
		display:block;
		* {
			transition: all .4s cubic-bezier(.71,-0.52,.35,1.63);
		}
		&:hover {
		 	.fa-arrow-right {
				transform: translateX(1rem);
			}
		}
	}
	.screen {
		position: absolute;
		overflow: hidden;
		box-shadow: 0.2rem 0.2rem 0.8rem 0 rgba(00, 00, 00, 0.2);
	}
	.transparency {
		opacity:.1;
		position: absolute;
		width:auto;
		height:auto;
		border-radius: .8rem;
	}
	.arrow-icon-circle {
		@include flex-row;
		justify-content: center;
		align-items: center;
		width:12rem;
		height:12rem;
		border-radius:50%;
		position:absolute;
		top:40%;
		right:0;
		z-index: 6;
		.fa-arrow-right {
			fill:#fff;
			height:2.8rem;
		}
	}
}

// RESPONSIVE
@media screen and (max-width: 1400px) {
	.landing-card  {
		width: calc(100vw - 8rem);
		margin: 4rem 4rem 8rem;
// content block	
		.block-content {
			padding-top: 12rem;
			padding-left: 0;
		}
		.title {
			font-size: 4.8rem;
		}
	}
}
@media screen and (max-width: 1140px) {
	.landing-card  {
		.para {
			font-size:1.6rem;
		}
// link block
		.block-link {
			.arrow-icon-circle {
				width:10rem;
				height:10rem;
			}
		}
	}
}
@media screen and (max-width: 960px) {
	.landing-card  {
	    &:before {
			font-size:6rem;
		}
		// content block	
		.block-content {
			padding-top:8rem;
		}
		.title {
			font-size: 4rem;
		}
	}
}
@media screen and (max-width: 760px) {
	.landing-card  {
		@include flex-column;
		margin-bottom:0;
// content block	
		.block-content {
			width:100%;
			padding-right:0;
			padding-bottom:0;
		}
// link block
		.block-link {
			position:relative;
			width:100%;
			min-height: 36rem;
			.arrow-icon-circle {
				right:50%;
				transform: translateX(50%);
				width:12rem;
				height:12rem;
			}
		}
	}
}
@media screen and (max-width: 680px) {
	.landing-card  {
		width: calc(100vw - 4.8rem);
		margin-left: 2.4rem;
		margin-right: 2.4rem;
		.block-link {
			.screen {
				top: 2.4rem;
				left: 0;
				width: 100%;
			}
		}
	}
}
@media screen and (max-width: 375px) {

	.landing-card  {
	}
}
