/* ==========
TYPOGRAPHY
========== */

html {
  font-size: 62.5%;
  font-weight: 400;
  font-style: normal;
  padding: 0;
  margin: 0;
}

@mixin app-font {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

body {
  @include app-font;
  color: $primary-1;
  font-size: 1.4rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4 {
  @include app-font;
  font-weight: 600;
  font-style: normal;
}

// h2 level
@mixin folio-card-title {
  font-size: 4rem;
  line-height: 1.1;
  margin-top: 3.2rem;
  margin-bottom: 3.2rem;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: -2.8rem;
    left: 0;
    height: 0.8rem;
    width: 6.4rem;
  }
}

