// new case study template
.section-title {
  font-size:3.2rem;
  margin-bottom:2.8rem;
}

.section-title-meta {
  color:#bbb; // default
  font-size:2rem;
  text-transform: uppercase;
  letter-spacing: .2rem;
}

.block-title {
  text-transform: uppercase;
  letter-spacing: .2rem;
  margin-bottom:1.6rem;
}

p {
  font-size: 1.8rem;
  line-height: 1.4;
  margin-bottom: 2.4rem;

  span {
    white-space: nowrap;
  }
}

a {
  color: $primary-1;
  position: relative;

  &:hover {
    text-decoration: none;
    color: inherit;
  }
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

figcaption {
  font-size: 1.4rem;
  line-height:1.2;
  margin: .8rem 0 2.4em;
  display:block;
}

.link-inline {
  display: inline-block;
  position: relative;

  &:before {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(252, 252, 2, 0.5);
    transform-origin: bottom;
    transform: scaleY(0.1);
    @include transition02;
    content: "";
  }

  &:hover:before {
    transform: scaleY(1);
  }
}


.quote {
  width: 100%;
  max-width: 72rem;
  margin: 0 auto;
  padding: 0 4rem;
  position: relative;

  .statement {
    font-size: 4rem;
    font-weight: 300;
    line-height:1.2;
    text-align: center;
    padding-top: 2rem;
    border-top: 1px solid #eee;
    display:block;
  }

  .personna {
    text-align: center;
    margin-top: 1.2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid #eee;
    display:block;
  }

  .fa-angle-double-left,
  .fa-angle-double-right {
    fill: #ddd;
    width:2.8rem;
    position: absolute;
    top: 2rem;
    left: 2rem;
    right: auto;
    bottom: auto;
    display:block;
  }
  .fa-angle-double-right {
    top: auto;
    left: auto;
    right: 2rem;
    bottom: 2rem;
  }
}