

.landing-card.cs-cabins {
    border-top-color: $cs-green;
	&:before {
        content:'03';
        color: $cs-green;
    }
// content block    
    .category {
        color: $cs-green;
    }
    .logo-block {
        .logo-iglu {
            display:inline-block;
            height: 2.4rem;
            width: auto;
            margin-right:4.8rem;
        }
    }
// link block   
    .block-link {
        .screen {
            @include flex-column;
            justify-content: center;
            z-index:3;
            position: absolute;
            top:5%;
            left:10%;
            width:80%;
            height:95%;
            border-radius:.6rem;
        }
        .arrow-icon-circle {
            background-color: $cs-green;
        }
        .transparency {
            background-color: $cs-green;
        }
        .transparency.pos01 {
            top:12%;
            right:30%;
            bottom:0;
            left:6%;
            z-index:1;
        }
        .transparency.pos02 {
            top:2%;
            right:4%;
            bottom:2%;
            left:20%;
            z-index:2;
        }
        .transparency.pos03 {
            top:20%;
            right:30%;
            bottom:0;
            left:4%;
            transform: rotate(-3deg);
            z-index:4;
        }
// hover        
        &:hover {
            .transparency.pos01 {transform: rotate(0) scale(1.4,1); transform-origin: bottom left;}
            .transparency.pos02 {transform: rotate(0) scale(.9,1); transform-origin: top right;}
            .transparency.pos03 {transform: rotate(-1deg) scale(1.1,1.1); transform-origin: top left;}
		}
  	}
}


// BREAKPOINTS
@media screen and (max-width: 680px) {
    .landing-card.cs-cabins {
		.block-link {
			.screen {
				top: 2.4rem;
				left: 0;
				width: 100%;
			}
            .transparency.pos01 {
                top:0;
                right:30%;
                bottom:-2rem;
                left:6%;
            }
            .transparency.pos02 {
                top:2%;
                right:14%;
                bottom:2%;
                left:20%;
            }
            .transparency.pos03 {
                top:20%;
                right:0;
                bottom:0;
                left:10%;
                transform: rotate(5deg);
            }
		}
    }
}
